  .checkbox-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer; }
  .checkbox-container .checkmark {
    position: absolute;
    top: -2px;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #eee;
    outline: 1px solid #009688; }
